.archive {
  padding-top: 20px;

  .archive-title {
    font-family: $global-serif-font-family;

    &.tag,
    &.category {
      margin: 15px 0;
    }

    .archive-name {
      margin: 0;
      display: inline-block;
      font-weight: 400;
      font-size: $archive-name-font-size;
      line-height: $archive-name-font-size + 2px;
    }

    .archive-post-counter {
      color: $dark-gray;
    }
  }

  .collection-title {
    font-family: $global-serif-font-family;

    .archive-year {
      margin: 5px 0;
      font-weight: bold;
      font-size: 21px;
      line-height: 1.19048;
      font-weight: 600;
      letter-spacing: .011em;
      border-bottom: 1px solid rgba(115, 115, 115, 0.1);
    }
  }

  .archive-post {
    padding: $archive-post-padding;
    border: dotted 1px #dedede;
    border-width: 0 0 1px;
    padding-left: 50px;
    padding-right: 20px;

    .archive-post-time {
      color: #888;
      float: right;
      font-family: "Segoe UI", "SegoeUI", "Helvetica Neue", Helvetica, Arial, sans-serif;
    }

    .archive-post-title {

      .archive-post-link {
        color: rgba(0, 0, 0, .8);
      }
    }

    &::first-child {
      margin-top: 10px;
    }

    &:hover {
      // transition: $archive-post-hover-transition;
      // transform: $archive-post-hover-transform;

      .archive-post-time {
        color: darken($dark-gray, 10%);
      }

      .archive-post-title .archive-post-link {
        color: darken($dark-gray, 100%);
      }
    }
  }
}

@include max-screen() {
  .archive {
    margin-left: auto;
    margin-right: auto;
    padding-top: 5px;

    .archive-title .archive-name {
      font-size: $archive-name-font-size - 4px;
    }

    .collection-title .archive-year {
      margin: 10px 0;
      font-size: $collection-title-font-size - 4px;
    }

    .archive-post {
      // padding: $archive-post-mobile-padding;
      padding: 3px 0px 3px 10px;

      .archive-post-time {
        display: block;
      }
    }
  }
}

.archive-header {
  height: 36px;
  border-bottom: 1px solid #e6e6e6;
}

.archive-header-title {
  float: left;
  padding-left: 10px;
  line-height: 36px;
}

.archive-header-logo {
  float: left;
  width: 16px;
  height: 100%;
  background-size: 16px 16px;
  background-repeat: no-repeat;
  background-position: center center;
  background-image: url(../../../../static/img/archive-header-logo.png);
}