/*鼠标效果*/
.no-touch {
  .newlandingtile:hover {
    box-shadow: 10px 10px 20px 0 rgba(0, 0, 0, 0.04), -10px 0 20px 0px rgba(0, 0, 0, 0.04);
    z-index: 1;

    .tile-content-text {
      box-shadow: none;
      z-index: initial
    }

    .tile-content-text {
      transform: translate(0, -46px);
    }

    .tile-media {
      .image-asset {
        transform: scale(1.05)
      }
    }

    .tile-media:after {
      content: '';
      display: block;
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      background-color: rgba(0, 0, 0, 0.15)
    }

    .tile-subhead {
      transition: opacity 180ms ease 180ms;
      opacity: 1
    }
  }

  .landingtile-hero:hover {
    .tile-content-text {
      transform: translate(-8px, 0);
    }
  }

  @media only screen and (max-width: 735px) {
    .newlandingtile:hover {
      box-shadow: none;
      z-index: initial;

      .tile-media {
        .image-asset {
          transform: scale(1)
        }
      }

      .tile-content-text {
        transform: translate(0, 0)
      }

      .tile-media:after {
        background-color: transparent
      }
    }
    .landingtile-hero:hover {
      .tile-content-text {
        transform: translate(0, 0)
      }
    }
  }
}

.landingtile-wide {
  .tile-asset {
    padding-top: 56.25%
  }
}

